'use client'

import { PromptFile } from '@/lib/prompts'
import { FileText, Calendar, Hash } from 'lucide-react'
import Link from 'next/link'
import { Card, CardContent, CardHeader } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'

interface PromptCardProps {
  prompt: PromptFile
}

export default function PromptCard({ prompt }: PromptCardProps) {
  const formatDate = (dateString: string) => {
    return new Intl.DateTimeFormat('en-US', {
      month: 'short',
      day: 'numeric',
      year: 'numeric'
    }).format(new Date(dateString))
  }

  const truncateContent = (content: string, maxLength: number = 150) => {
    if (content.length <= maxLength) return content
    return content.substring(0, maxLength).trim() + '...'
  }

  return (
    <Link href={`/prompt/${prompt.id}`} className="block transition-all duration-300 hover:scale-[1.01]">
      <Card className="group relative overflow-hidden bg-card/90 backdrop-blur-md border-border/50 hover:border-primary/30 transition-all duration-300 hover:shadow-lg hover:shadow-primary/10">
        {/* Modern accent line */}
        <div className="absolute left-0 top-0 w-1 h-full bg-gradient-to-b from-primary via-primary/80 to-primary/60 opacity-60 group-hover:opacity-100 group-hover:w-2 transition-all duration-300"></div>

        <CardHeader className="pb-3">
          <div className="flex items-start justify-between mb-3">
            <div className="flex items-center gap-3">
              <div className="p-2.5 bg-primary/10 rounded-xl border border-primary/20 group-hover:bg-primary/15 transition-colors">
                <FileText className="h-4 w-4 text-primary" />
              </div>
              <Badge variant="secondary" className="bg-muted/80 text-muted-foreground border-border/50 font-medium">
                {prompt.category}
              </Badge>
            </div>
            <Badge variant="outline" className="bg-muted/50 text-muted-foreground border-border/50">
              <Hash className="h-3 w-3 mr-1" />
              {prompt.wordCount} words
            </Badge>
          </div>

          <h3 className="text-xl font-semibold text-foreground group-hover:text-primary transition-colors leading-tight">
            {prompt.title}
          </h3>
        </CardHeader>

        <CardContent className="pt-0">
          {prompt.description && (
            <p className="text-sm text-muted-foreground mb-4 line-clamp-2 leading-relaxed">
              {prompt.description}
            </p>
          )}

          <p className="text-sm text-muted-foreground/80 mb-6 line-clamp-3 leading-relaxed">
            {truncateContent(prompt.content)}
          </p>

          <div className="flex items-center justify-between">
            <div className="flex items-center gap-2 text-xs text-muted-foreground">
              <Calendar className="h-4 w-4 text-primary" />
              <span className="font-medium">{formatDate(prompt.lastModified)}</span>
            </div>

            {prompt.tags.length > 0 && (
              <div className="flex gap-2">
                {prompt.tags.slice(0, 2).map((tag, index) => (
                  <Badge
                    key={index}
                    variant="outline"
                    className="bg-primary/5 text-primary border-primary/20 hover:bg-primary/10 transition-colors text-xs"
                  >
                    {tag}
                  </Badge>
                ))}
                {prompt.tags.length > 2 && (
                  <Badge
                    variant="outline"
                    className="bg-primary/5 text-primary border-primary/20 hover:bg-primary/10 transition-colors text-xs"
                  >
                    +{prompt.tags.length - 2}
                  </Badge>
                )}
              </div>
            )}
          </div>
        </CardContent>
      </Card>
    </Link>
  )
}