'use client'

import { PromptCategory } from '@/lib/prompts'
import { Button } from '@/components/ui/button'
import { Badge } from '@/components/ui/badge'

interface CategoryFilterProps {
  categories: PromptCategory[]
  selectedCategory: string | null
  onCategorySelect: (category: string | null) => void
}

export default function CategoryFilter({
  categories,
  selectedCategory,
  onCategorySelect
}: CategoryFilterProps) {
  return (
    <div className="space-y-1">
      <Button
        onClick={() => onCategorySelect(null)}
        variant={selectedCategory === null ? "default" : "ghost"}
        className={`w-full justify-between h-auto p-4 ${
          selectedCategory === null
            ? 'bg-primary text-primary-foreground hover:bg-primary/90 shadow-sm'
            : 'bg-card/50 text-foreground hover:bg-card/80 hover:text-primary border border-border/50'
        } transition-all duration-200 backdrop-blur-sm`}
      >
        <span className="font-medium">All Prompts</span>
        <Badge
          variant={selectedCategory === null ? "secondary" : "outline"}
          className={selectedCategory === null
            ? "bg-primary-foreground/20 text-primary-foreground border-0"
            : "bg-muted/60 text-muted-foreground border-border/50"
          }
        >
          {categories.reduce((sum, cat) => sum + cat.count, 0)}
        </Badge>
      </Button>

      {categories.map((category) => (
        <Button
          key={category.name}
          onClick={() => onCategorySelect(category.name)}
          variant={selectedCategory === category.name ? "default" : "ghost"}
          className={`w-full justify-between h-auto p-4 ${
            selectedCategory === category.name
              ? 'bg-primary text-primary-foreground hover:bg-primary/90 shadow-sm'
              : 'bg-card/50 text-foreground hover:bg-card/80 hover:text-primary border border-border/50'
          } transition-all duration-200 backdrop-blur-sm`}
        >
          <span className="truncate font-medium">{category.name}</span>
          <Badge
            variant={selectedCategory === category.name ? "secondary" : "outline"}
            className={selectedCategory === category.name
              ? "bg-primary-foreground/20 text-primary-foreground border-0"
              : "bg-muted/60 text-muted-foreground border-border/50"
            }
          >
            {category.count}
          </Badge>
        </Button>
      ))}
    </div>
  )
}